@import url(https://fonts.googleapis.com/css2?family=Luckiest+Guy&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,100;0,300;0,400;0,500;0,700;0,900;1,100;1,300;1,400;1,500;1,700;1,900&display=swap);
.main-page__content {
  padding: 42px calc(50% - 500px);
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}

.main-page__main-section {
  padding-left: 40px;
  box-sizing: border-box;
  /* width: 60%; */
  width: 400px;
}

.main-page__search-box {
  margin-bottom: 35px;
}

.main-page__favorites {
  /* width: 35%; */
  width: 300px;
  padding-left: 40px;
}

.header {
  background: rgb(69, 8, 166);
  padding: 20px calc(50% - 500px) 10px;
}

.header__title {
  font-family: "Roboto";
  color: #ffeedb;
  font-size: 45px;
  margin-left: 40px;
}

.moto {
  font-size: 16px;
}
/* #496DDB */

.search-box__form {
  display: flex;
}

.search-box__form-label {
  display: inline-block;
  font-size: 12px;
  margin-right: 10px;
}

.search-box__form-input {
  box-sizing: border-box;
  display: block;
  margin-top: 5px;
  width: 300px;
  font-size: 14px;
  padding: 5px 10px;
  border: 1px solid rgba(0, 8, 7, 0.5);
}

.search-box__form-submit {
  margin-top: 17px;
  padding: 5px 10px;
  border: none;
  transition: all 0.2s linear;
  color: #ffeedb;
  background-color: rgb(8, 92, 82);
}

.search-box__form-submit:hover:not(:disabled) {
  opacity: 0.8;
  cursor: pointer;
  transition: all 0.2s linear;
  background-color: #ffeedb;
  color: rgba(0, 8, 7);
}

.search-box__form-submit:disabled {
  opacity: 0.5;
  cursor: not-allowed;
}

.movie-item {
  padding: 20px;
  background-color: #884b9b;
  display: flex;
}

.movie-item__poster {
  height: 150px;
  margin-right: 21px;
}

.movie-item__title {
  font-weight: 500;
  font-size: 21px;
  margin-bottom: 14px;
}

.movie-item__add-button {
  border: none;
  background-color: rgb(8, 92, 82);
  padding: 10px 15px;
  color: #ffeedb;
  font-size: 14px;
  transition: all 0.2s linear;
}

.movie-item__add-button:hover {
  cursor: pointer;
  opacity: 0.9;
  transition: all 0.2s linear;
}

.movies__item {
    margin-bottom: 21px;
}
.favorites {
  box-sizing: border-box;
  border: 1px solid rgb(53, 61, 60);
  padding: 30px 20px;
}

.favorites__name {
  box-sizing: border-box;
  width: 100%;
  font-size: 16px;
  padding: 5px 10px;
  border: 1px solid rgba(61, 68, 67, 0.5);
  margin-bottom: 24px;
}

.favorites__list {
  margin-bottom: 24px;
}

.favorites__save {
  border: none;
  background-color: rgb(166, 8, 113);
  padding: 10px 15px;
  color: #ffeedb;
  font-size: 14px;
  transition: all 0.2s linear;
}

.favorites__save:hover {
  cursor: pointer;
  opacity: 0.9;
  transition: all 0.2s linear;
}

.favorites input {
  margin-top: 15px;
}

.list-page {
  padding: 42px calc(50% - 300px);
}

.list-page__title {
  font-size: 34px;
  margin-bottom: 26px;
}

.list-page ul li {
  font-size: 18px;
  margin-bottom: 20px;
}

.posterPic {
  width: 100px;
  margin-top: 20px;
}

/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/

html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed, 
figure, figcaption, footer, header, hgroup, 
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
	margin: 0;
	padding: 0;
	border: 0;
	font-size: 100%;
	font: inherit;
	vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure, 
footer, header, hgroup, menu, nav, section {
	display: block;
}
body {
	line-height: 1;
}
ol, ul {
	list-style: none;
}
blockquote, q {
	quotes: none;
}
blockquote:before, blockquote:after,
q:before, q:after {
	content: '';
	content: none;
}
table {
	border-collapse: collapse;
	border-spacing: 0;
}
/* Color palette https://coolors.co/ffeedb-e63946-6a8d73-000807-496ddb */

body, button, input {
    font-family: 'Roboto', 'Helvetica', sans-serif;
    color: #000807;
}


